<template>
  <div class="container">
    <div class="row justify-content-center">
      <div class="col-lg-6 col-md-8 col-10">
        <div class="jumbotron">
          <hr>
          <p v-if="error.length===0">Fill up the form to Log In</p>
          <p v-else class="errorMessage" style="font-size: 16px">
            <i class="fas fa-exclamation-triangle fa-lg"></i>
            <span> {{ error }} </span>
          </p>
          <hr>
        </div>

        <form @submit.prevent="login">
          <!-- Username -->
          <template>
            <div class="input-group form-group">
              <div class="input-group-prepend">
                <div class="input-group-text"> <i class="fas fa-user fa-fw"></i> </div>
              </div>

              <input 
                @blur="$v.info.username.$touch()"
                v-model="info.username"
                class="form-control"
                :class="{'is-invalid': $v.info.username.$error}"
                id="formUsername"
                placeholder="Username"
                name="username" 
                type="text"
              >
              <label class="sr-only" for="formUsernamme">Username</label>
            </div>
            <!-- <template v-if="$v.info.username.$error">
                <p v-if="!$v.info.username.$required" class="errorMessage text-left">
                  <i class="fas fa-exclamation-triangle fa-lg"></i>
                  <span> Username required </span>
                </p>
            </template> -->
          </template>

          <!-- Password -->
          <template>
            <div class="input-group form-group" id="pass">
              <label class="sr-only" for="formPassword">Password</label>
              <div class="input-group-prepend">
                  <div class="input-group-text"> <i class="fas fa-lock fa-fw"></i> </div>
              </div>
              <input 
                @blur="$v.info.password.$touch()"
                v-model="info.password"
                class="form-control"
                :class="{'is-invalid': $v.info.password.$error}"
                id="formPassword" 
                placeholder="Password" 
                name="password"
                type="password"
              >
            </div>
            <!-- <template v-if="$v.info.password.$error">
              <p v-if="!$v.info.password.$required" class="errorMessage text-left">
                <i class="fas fa-exclamation-triangle fa-lg"></i>
                <span> Password required </span>
              </p>
            </template> -->
          </template>
          
          
          <!-- Log in button -->
          <template>
            <button 
              @click="$v.$touch()"
              :disabled="$v.$anyError"
              type="submit"
              class="btn btn-primary btn-lg btn-block"
            > 
              Log in
            </button>
          </template>
      
          <!-- Sign up link -->
          <div id="goRegister">
            <p>
              <span> Do not have an accout? </span>
              <router-link :to="{ name: 'Signup'}" > <a> Register Here </a> </router-link>
            </p>
          </div>

        </form>
      </div>
    </div>

  </div>
</template>

<script>
  import utils from '@/utils.js';
  import NProgress from 'nprogress';
  import EventService from '@/services/EventService.js';
  import { required } from 'vuelidate/lib/validators';

  export default {
    name: 'LogIn',
    data(){
      return {
        isLoggedIn: false,
        info: {
          username: '',
          password: ''
        },
        error: '',
      }
    },    

    methods: {
      async login(){
        NProgress.start();

        await this.checkLoginStatus();

        if(!this.isLoggedIn) {
          EventService.logIn({password: this.info.password, username: this.info.username})
            .then((res)=>{
              this.$store.dispatch('postStore/saveUser', res.data[0]);
              this.$store.dispatch('notificationStore/showNotification', res.data[1])
              if (res.data[1].catagory.toLowerCase() === 'success'){
                this.$router.push({name: 'Home'})
              }
              NProgress.done();
            })
            .catch((err)=>{
              this.$store.dispatch('notificationStore/showNotification', {catagory: 'error', title: 'Connection Problem', message: err.message});
              NProgress.done();
            })
          } else {
            this.$store.dispatch('notificationStore/showNotification', {catagory: 'warning', title: 'Not required', message: 'You are already logged in'});
            NProgress.done();
          }
      },

      checkLoginStatus (){
        return EventService.isLoggedIn()
          .then (res => {
            this.isLoggedIn = res.data[1];
            this.$store.dispatch('postStore/saveUser', res.data[0]);
          })
          .catch (err => {throw err})
      },
    },

    validations: {
      info: {
        username: { required },
        password: { required }
      }

    },

    components: {
      
    }
      
  }
</script>

<style lang="css" scoped>
  /* --------------------( body )-------------------- */
  body {
    margin: 1px auto 60px auto;
    font-family: "Kite One, ubuntu";
  }

  /* --------------------( ^ body )-------------------- */

  .jumbotron {
    margin-top: 2%;
    margin-bottom: 0;
    padding-top: 20px;
    padding-bottom:5px;
    text-align: center;
    font-family: ubuntu;
  }

  form {
    margin: 10% auto;
    font-family: 'Andika New Basic';
  }

  #pass {
    margin-top: 6%;
  }

  form button {
    margin-top: 10%;
    font-family: 'Andika New Basic';
    width: 200px;
  }

  #goRegister {
    text-align: right;
    margin-top: 5%;
    margin-right: 10px;
  }

  #goRegister a {
    font-family: 'langar';
  }

  .error-box {
    box-shadow: 5px 5px 10px red;
  }

  .errorMessage {
    margin-left: 20px;
    font-family: 'Kite One';
    font-size: 14px;
  }

  .errorMessage i {
    color: red;
  }

  .errorMessage span {
    margin-left: 5px;
    color: red;
    letter-spacing: 1px;
    font-weight: bolder;
    text-shadow: 5px 5px 8px #676565;
  }
</style>